import { reqSearchList } from '@/api/index';
const state = {
    searchList: {},
};
const mutations = {
    GETSEARCHLIST(state, searchList) {
        state.searchList = searchList;
    }
};
const actions = {
    async getSearchList({ commit }, params = {}) {
        // 注意，在发reqFloorList函数获取数据时，至少传递一个参数： 空对象。否则会报错。
        // params形参：是当前用户派发ations的时候传的。 第二个参数传递过来的，至少是一个空对象
        const result = await reqSearchList(params);
        // console.log(result);
        if (result.code == 200) {
            commit('GETSEARCHLIST', result.data)
        }
    }
};

// 计算属性，在项目中，为了简化数据而生
//项目当中getters的主要作用是：简化仓库中的数据
// 可以把我们将来在组件当中需要的数据简化一下。将来组件在获取数据的时候就方便了
const getters = {
    // 注意：state是分仓库的，而getters是不分仓库的。当前的state是当前search组件中的
    goodsList(state){
        return state.searchList.goodsList||[];  //注意：假如网络不给力，返回的结果是undefined.
        // 所以需要给它一个空数组或者是对象
    },
    attrsList(state){
        return state.searchList.attrsList||[];
    },
    trademarkList(state){
        return state.searchList.trademarkList||[];
    }
};

export default {
    state,
    mutations,
    actions,
    getters
}