// 当前模块是对接口进行统一管理
import requests from './request';
import mockRequests from './mockAjax';
// 三级联动接口
// /api/product/getBaseCategoryList  get请求。  无参
// axios发送请求的结果都是promise对象
export const reqCategoryList = () => {
  return requests({ url: "/product/getBaseCategoryList", method: "get" });
};

// 轮播图banner数据接口, 路径/banners。  get请求，无参
export const reqBannerList = () => {
  return mockRequests({
    url: '/banners',
    mothod: 'get'
  })
};

// floors组件中，获取轮播图的数据  get请求，无参
export const reqFloorList = () => {
  return mockRequests.get('/floors');
};

// search组件，发请求获取数据。
// post请求，   url：/api/list   
// 参数 10个。不是必选
// {
//   "category3Id": "61",
//   "categoryName": "手机",
//   "keyword": "小米",
//   "order": "1:desc",
//   "pageNo": 1,
//   "pageSize": 10,
//   "props": ["1:1700-2799:价格", "2:6.65-6.74英寸:屏幕尺寸"],
//   "trademark": "4:小米"
// }
// 当前这个函数需不需要接受外部传递参数
// 当前这个接口，给服务器传递一个默认参数。注意：这个默认参数至少是个空对象: params={}
export const reqSearchList = (params) => {
  return requests({
    url: '/list',
    method:'post',
    data: params, 
  })
}