
import { reqCategoryList, reqBannerList, reqFloorList } from '@/api/index';
const state = {
    categoryList: [],
    bannersList: [],
    floorList: [],
};
const mutations = {
    CATEGORYLIST(state, categoryList) {
        state.categoryList = categoryList
    },
    GETBANNERSLIST(state, bannersList) {
        state.bannersList = bannersList
    },
    GETFLOORSLIST(state, floorList) {
        state.floorList = floorList
    }
};
const actions = {
    async gettypeNavList({ commit }) {
        const result = await reqCategoryList();
        //  console.log(result);
        if (result.code) { commit('CATEGORYLIST', result.data); }
    },
    async getBannerList({ commit }) {
        const result = await reqBannerList();
        //  console.log(result);
        if (result.code == 200) {
            commit('GETBANNERSLIST', result.data)
        }
    },
    async getFloorsList({ commit }) {
        const result = await reqFloorList();
        // console.log(result);
        if (result.code == 200) {
            commit('GETFLOORSLIST', result.data)
        }
    },
};
const getters = {};

export default {
    state,
    mutations,
    actions,
    getters,
}